import React from 'react';
import {graphql, useStaticQuery} from 'gatsby';
import {css} from '@emotion/core';
import styled from '@emotion/styled';
import Image from 'gatsby-image';

const TextoAcerca = styled.div`
  padding-top: 4rem;
  max-width: 1000px;
  width: 95%;
  margin: 0 auto;

  @media(min-width: 768px){
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
  p[
      line-height: 2;
  ]
`;

const ContenidoAcerca = () =>{
  const resultado = useStaticQuery(graphql`

{
  allDatoCmsPagina(filter: {slug: {eq: "acerca"}}){
    nodes {
      titulo,
      contenido,
      imagen{
        fluid ( maxWidth: 1200){
	  ...GatsbyDatoCmsFluid
        }
      }
    }
  }
}
  `);
  console.log(resultado.allDatoCmsPagina.nodes[0]);
  const {contenido, titulo, imagen} = resultado.allDatoCmsPagina.nodes[0];
  return(
    <>
    	    <h2
	      css={css`
		text-align: center;
		font-size: 4rem;
		margin-top: 4rem;
		`}
	  >{titulo}</h2>
	  <TextoAcerca> 
	   <p> {contenido}</p>
	  <Image fluid={imagen.fluid} />
	  </TextoAcerca>
	  </>
  );

}
export default ContenidoAcerca;
